import React, {useEffect, useState} from "react";
import {useCookies} from 'react-cookie';
import Loading from "./Loading";
import {useNavigate} from 'react-router-dom';

function NumberPage(props){
    document.title = "HM Shop | Order Code"
    let nav = useNavigate();
    let [numberCookie, setNumberCookie, RemoveNumberCookie] = useCookies(['number']);
    let [number, setNumber] = useState(0);
    let [auth, setAuth] = useState(false);
    useEffect(()=>{
        if(!numberCookie.number){
            nav('/');
            }else{
            setTimeout(() => {
              setAuth(true);
            }, 1000);
            setNumber(numberCookie.number);
            }
        setTimeout(()=>{
            setNumberCookie('number', 0);
        RemoveNumberCookie('number');
        }, 1000);
    }, []);

    
    return (
        <>
        {auth ?
        <div className="container mx-auto px-2 xl:px-0 flex flex-col">
        <div className="px-2 xl:px-0 ">
          <div className="w-full h-fit py-8 px-4 flex justify-center items-center rounded mt-4 mb-1 bg-gray-700 text-white text-2xl">
          Order Code
          </div>
        </div>
       <div className="w-full px-2 lg:px-0 my-4 flex flex-col rounded min-h-[470px] lg:min-h-[520px]">
         <div className="flex flex-col lg:flex-row items-center justify-center">
          <div className="flex flex-col justify-center pt-10 pb-8 md:pb-2 text-center">
            <span className="text-xl">Your Code</span>
            <div className="flex justify-center my-4">
              <h6 className=" border-2 border-gray-400 text-black rounded w-fit py-2 px-4 shadow-sm text-2xl">{number}</h6>
             </div>
             <div className="border-t-2 border-gray-500 py-2">
                Thanks for your purchase. Visit us again.
             </div>
           </div>
         </div>
         
       </div>
                </div>
                :
                <Loading />
                }
        </>
    )
}

export default NumberPage;